body{
    background-color: #e0e0e0;
}

.todo-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 400px;
    margin: 0 auto;
    padding: 20px;
    background-color: #333333;
    box-shadow: 5px 5px 20px rgba(51, 51, 51, 0.8), -5px -5px 20px rgba(51, 51, 51, 0.8);
    border-radius: 10px;
}

h2{
    color: rgb(237, 182, 80);
}

input[type='text']{
    width: 100%;
    box-sizing: border-box;
    margin-bottom: 10px;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
}

button{
    margin-right: 5px;
    padding: 8px 12px;
    border: none;
    border-radius: 4px;
    background-color: #b79439;
    color: #fff;
    cursor: pointer;
}

button:hover{
    background-color: #e07722;
}

ul{
    list-style-type: none;
    padding: 0;
}

li{
    margin-bottom: 5px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: #fff;
}
li button{
    margin-left: 15px;
    background-color: #e74c3c;
    flex-shrink: 0;
}